import React, { useRef, useEffect, useState } from "react";
import { Col, Row, Container, Button } from "react-bootstrap";
import { navigate } from "gatsby";

import "../components/pages-css/sCita.css";
import Layout from "../components/layout.js";
import Seo from "../components/seo";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Timer from "../components/timer.js";

import servicio from "../images/iconos/btn_servicio.svg";
import diagnostico from "../images/iconos/btn_diagnostico.svg";

export default function ServicioRealizar(props){

    const [rutaLink, setRutaLink] = useState();
    const [click, setClick] = useState(false);
    const [error, setError] = useState(0);
    const [modal, setModal] = useState(false);

    useEffect(() => {
        if (typeof window !== 'undefined') {
            //console.log("tengo lo siguiente jiji: ", localStorage.getItem("RutaNavegacion"));

            if(localStorage.getItem("RutaNavegacion") === "Orden-reparacion"){
                setRutaLink("/orden-reparacion");
            } else if (localStorage.getItem("RutaNavegacion") === "nuevo-usuario") {
                setRutaLink("/registrar-vehiculo");
            } else if (localStorage.getItem("RutaNavegacion") === "sin-cita") {
                setRutaLink("/datos-facturacion");
            }
        } else {
            //console.log('we are running on the server');
        }
    }, [])
//8118209288
    const refForm = useRef();

    return(
        <Layout backUrl={rutaLink}>
            <Seo title="Auto servicio" />
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            <Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>
                <Row className="row-cita-arriba">
                    <div className="container-cita">
                        <Row>
                            <Col className="titulo-cita"><h1 className="titulo-h1">Permítenos ayudarte sobre tu Servicio a realizar</h1></Col>
                        </Row>
                        <Container className="p-5">
                            <Row>
                                <Col lg={6} style={{display: 'flex', justifyContent: 'center'}}>
                                    <Row className="btn-celeste-grande" onClick={() => {navigate("/realizar-servicio")}}>
                                        <div style={{height: '80%', display: 'flex', justifyContent: 'center'}}>
                                            <img src={servicio} className="img-servicios"/>
                                        </div>
                                        <div style={{display: 'flex', justifyContent: 'center'}}><h5 >Servicio de Mantenimiento</h5></div>
                                    </Row>
                                </Col>
                                <Col lg={6} style={{display: 'flex', justifyContent: 'center'}}>
                                    <Row className="btn-celeste-grande" onClick={() => {navigate("/realizar-diagnostico")}}>
                                        <div style={{height: '80%', display: 'flex', justifyContent: 'center'}}>
                                            <img src={diagnostico} className="img-servicios"/>
                                        </div>
                                        <div style={{display: 'flex', justifyContent: 'center'}}><h5 >Diagnóstico</h5></div>
                                    </Row>
                                </Col>
                            </Row>
                        </Container>
                    </div>
                </Row>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="none"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} registro={false} setClick={setClick}/>
        </Layout>
    )

}